        /** Get Table Data AJAX REQUEST -> PROMISE
         * Description: This will fetch all users currently in the Test Centre room
         * the callback will resolve with all the data.
         * @return resolved data
         *
         * FIXME: This is not done
         */
        getInitialData: function () {
            return TCSLib.jaxyPromise({
                'call': 'getAllStats',
                'params': {},
                'class': 'Stats'
            });
        },

        /** Get Table Data AJAX REQUEST -> PROMISE
         * Description: This will have all the general stats in a table
         * @return resolved data
         */
        getTableStats: function () {
            return TCSLib.jaxyPromise({
                'call': 'getTableStats',
                'params': {},
                'class': 'Stats'
            });
        },

        /** Get Graph Data AJAX REQUEST -> PROMISE
         * Description: Let's build a graph to show the number exams written for the week
         * @return resolved data
         */
        getGraphData: function (graph_range, to_call, params) {
            return TCSLib.jaxyPromise({
                'call': to_call,
                'params': params,
                'class': 'Stats'
            });
        },

        /** Build the Graph based on the data returned from Server AJAX REQUEST -> PROMISE
         * Description:
         * @params - json obj containing graph data
         * @return nothing, render it
         */
        buildGraph1: function (graph_range) {

            var ctx = document.getElementById('test_tcs_graph_chart1').getContext('2d'),
                start_range = (graph_range * -1),
                glabels = Array();
                // that = this;
            for (var i = start_range; i < graph_range; i++) {
                if (i < 0) {
                    glabels.push(i + ' days ago');
                } else if (i == 0) {
                    glabels.push('Today');
                } else {
                    glabels.push(i + ' days from now');
                }
            }
            // console.log("buildGraph1() -> What is the glabels for this chart: " + glabels);
            this.tcs_graph1 = new ChartJS(ctx, {
                type: 'line',
                data: {
                    labels: glabels,
                    datasets: [{
                        label: 'Max Potential Students',
                        data: [],
                        // backgroundColor: 'rgba(220,53,69, 1)'
                        borderColor: 'rgba(220,53,69, 1)',
                        fill: false
                        // borderWidth: 1
                    }
                    // {
                    //     label: 'Exams Open This Day',
                    //     data: [],
                    //     // backgroundColor: 'rgba(24,162,184, 1)'
                    //     borderColor: 'rgba(24,162,184, 1)',
                    //     fill: false
                    //     // borderWidth: 1
                    // }
                    ]
                },
                options: {
                    scales: {
                        yAxes: [{
                            ticks: {
                                // beginAtZero: true
                            }
                        }]
                    }
                }
            });

            window.tcs_graph1_obj = this.tcs_graph1;
            return;
        },

